import { Component } from '@angular/core';
//import { DxButtonModule } from 'devextreme-angular'; 

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
    title = 'Angular 2 com DEVEXPRESS';
    msg : string;

    cliente = { id: 1, nome: "Ivan Oliveira"};

    checkBoxValue: boolean;
    indeterminateValue: boolean;

    emailValue: string;
    rules: any;    

    constructor() {
        this.emailValue = 'smith@corp.com';
        this.rules = { "X": /[02-9]/ };
    }    

    valueChanged(data) {
        this.emailValue = data.value.replace(/\s/g, "").toLowerCase() + "@corp.com";
    }    

    helloWorld() {
        console.log('Hello world!');
        this.msg= this.cliente.nome;
    }  
}



